
import { defineComponent, onMounted, onBeforeUnmount } from "vue";
import VirtualGamepad from "@/components/VirtualGamepad.vue";
import { SceneManager } from "./simulator";
import I18nPlugin from "@/plugins/i18nPlugin";
export default defineComponent({
  name: "Simulator",
  components: {
    VirtualGamepad
  },
  setup() {
    let sceneManager: SceneManager | null = null;
    const i18n = I18nPlugin.use();
    const resize = () => {
      sceneManager?.engine.resize();
    };
    const joystickUpdate = (evt: {
      lx: number;
      ly: number;
      rx: number;
      ry: number;
    }) => {
      sceneManager?.updateStick(evt.lx, evt.ly, evt.rx, evt.ry);
    };
    const keyUpdate = (evt: string) => {
      console.log(evt);
    };

    onMounted(() => {
      sceneManager = new SceneManager("blender", "loadingScreen");
      window.addEventListener("resize", resize);
    });
    onBeforeUnmount(() => {
      if (sceneManager) window.removeEventListener("resize", resize);
      sceneManager?.engine.stopRenderLoop();
      sceneManager?.engine.dispose();
    });
    return { joystickUpdate, keyUpdate, i18n };
  }
});
