import * as BABYLON from "@babylonjs/core";
export function assignLightmapOnMaterial(
  material: BABYLON.StandardMaterial,
  lightmap: BABYLON.Texture
) {
  material.lightmapTexture = lightmap;
  material.lightmapTexture.coordinatesIndex = 1;
  material.useLightmapAsShadowmap = true;
}

export class LoadingScreen implements BABYLON.ILoadingScreen {
  element: HTMLElement;
  loadingUIBackgroundColor: string;
  loadingUIText: string;
  constructor(id: string) {
    const element = document.getElementById(id);
    if (!element) throw "loading screen element is empty";
    this.element = element;
    this.loadingUIBackgroundColor = "";
    this.loadingUIText = "";
  }
  displayLoadingUI() {
    this.element.style.display = "flex";
    this.element.style.opacity = "1";
    this.element.style.transition = "opacity 2s";
  }
  hideLoadingUI() {
    this.element.style.opacity = "0";
    setTimeout(() => {
      this.element.style.display = "none";
    }, 2000);
  }
}
