import * as BABYLON from "@babylonjs/core";
import "@babylonjs/loaders";
import { LoadingScreen, assignLightmapOnMaterial } from "./simulatorHelper";
import { Color3 } from "@babylonjs/core";
interface JoyStick {
  lx: number;
  ly: number;
  rx: number;
  ry: number;
}
type DogState = "Static" | "Walk" | "Run";

export class SceneManager {
  engine: BABYLON.Engine;
  scene: BABYLON.Scene;
  camera: BABYLON.Camera | null;
  dogMeshes: BABYLON.AbstractMesh[];
  light1: BABYLON.Light;
  light2: BABYLON.Light;
  glassMaterial: BABYLON.StandardMaterial | null;
  fixedRenderList: BABYLON.Mesh[];
  joyStick: JoyStick;
  state: DogState;
  camTarget: BABYLON.Mesh | null;

  constructor(canvasId: string, loadingScreenId: string) {
    const canvas = document.getElementById(canvasId) as HTMLCanvasElement;
    if (canvas === null) throw "canvas not found";
    if (canvas.nodeName !== "CANVAS") throw "not HTMLCanvasElement";

    this.engine = new BABYLON.Engine(canvas, true, {
      preserveDrawingBuffer: true,
      stencil: true
    });

    this.engine.enableOfflineSupport = false;
    this.engine.loadingScreen = new LoadingScreen(loadingScreenId);
    this.scene = new BABYLON.Scene(this.engine);
    this.scene.animationPropertiesOverride = new BABYLON.AnimationPropertiesOverride();
    this.scene.animationPropertiesOverride.enableBlending = true;
    this.scene.animationPropertiesOverride.blendingSpeed = 1;
    this.scene.animationPropertiesOverride.loopMode = 1;
    this.dogMeshes = [];
    this.glassMaterial = null;
    this.fixedRenderList = [];
    this.joyStick = { lx: 0, ly: 0, rx: 0, ry: 0 };
    this.state = "Static";
    this.camera = null;
    this.camTarget = null;

    //light
    this.light1 = new BABYLON.HemisphericLight(
      "HemiLight1",
      new BABYLON.Vector3(0, 1, 0),
      this.scene
    );
    this.light1.intensity = 1;
    this.light2 = new BABYLON.HemisphericLight(
      "HemiLight2",
      new BABYLON.Vector3(0, -1, 0),
      this.scene
    );
    this.light2.intensity = 0.5;
    this.light1.specular = new Color3(0, 0, 0);
    this.light2.specular = new Color3(0, 0, 0);

    BABYLON.SceneLoader.Append("/3dAssets/", "sci_new.glb", this.scene, () => {
      const scene = this.scene;
      const lightmappedMeshes = [
        { name: "PillarWall", repeat: 2 },
        { name: "WallSection", repeat: 2 },
        { name: "Ground", repeat: 1 },
        { name: "Door", repeat: 1 }
      ];
      const lightmapType = "filtered";

      for (let i = 0; i < lightmappedMeshes.length; i++) {
        for (let j = 1; j <= lightmappedMeshes[i].repeat; j++) {
          const meshName = lightmappedMeshes[i].name + (j > 9 ? "" : "0") + j;
          const currentMesh = scene.getNodeByName(meshName) as BABYLON.Mesh;
          const currentMeshChildren = currentMesh.getChildren() as BABYLON.Mesh[];
          const currentLightmap = new BABYLON.Texture(
            "/3dAssets/" +
              lightmappedMeshes[i].name +
              "_" +
              lightmapType +
              ".jpg",
            scene,
            false,
            false
          );
          switch (currentMesh.getClassName()) {
            case "Mesh":
              assignLightmapOnMaterial(
                currentMesh.material as BABYLON.StandardMaterial,
                currentLightmap
              );
              break;
            case "TransformNode":
              for (let j = 0; j < currentMeshChildren.length; j++) {
                if (currentMeshChildren[j].material!.id !== "Reflection")
                  assignLightmapOnMaterial(
                    currentMeshChildren[j].material as BABYLON.StandardMaterial,
                    currentLightmap
                  );
              }
              break;
          }
        }
      }

      const meshChildren = scene.getNodeByName("Ground01")!.getChildren()[0];
      const glass = meshChildren as BABYLON.Mesh;
      glass.computeWorldMatrix(true);
      const glassWorldMatrix = glass.getWorldMatrix();
      const glassVertexData = glass.getVerticesData(
        "normal"
      ) as BABYLON.FloatArray;
      let glassNormal = new BABYLON.Vector3(
        glassVertexData[0],
        glassVertexData[1],
        glassVertexData[2]
      );
      glassNormal = BABYLON.Vector3.TransformNormal(
        glassNormal,
        glassWorldMatrix
      );
      const reflector = BABYLON.Plane.FromPositionAndNormal(
        glass.position,
        glassNormal.scale(-1)
      );
      const glassMaterial = glass.material as BABYLON.StandardMaterial;

      glassMaterial.reflectionTexture = new BABYLON.MirrorTexture(
        "mirror",
        512,
        scene,
        true
      );

      this.glassMaterial = glassMaterial;
      (glassMaterial.reflectionTexture as BABYLON.MirrorTexture).mirrorPlane = reflector;
      this.fixedRenderList = [
        scene.getNodeByName("Door01"),
        scene.getNodeByName("Door02"),
        scene.getNodeByName("WallSection01"),
        scene.getNodeByName("WallSection02"),
        scene.getNodeByName("WallSection03"),
        scene.getNodeByName("WallSection04"),
        scene.getNodeByName("WallSection05"),
        scene.getNodeByName("WallSection06"),
        scene.getNodeByName("WallSection07"),
        scene.getNodeByName("WallSection08"),
        scene.getNodeByName("PillarWall01"),
        scene.getNodeByName("PillarWall02"),
        scene.getNodeByName("PillarWall03"),
        scene.getNodeByName("PillarWall04"),
        scene.getNodeByName("PillarWall05"),
        scene.getNodeByName("PillarWall06"),
        ...scene.getNodeByName("PillarLight")!.getChildren(),
        scene.getNodeByName("WallLight"),
        scene.getNodeByName("DoorLight")
      ] as BABYLON.Mesh[];

      (glassMaterial.reflectionTexture as BABYLON.MirrorTexture).renderList = this.fixedRenderList;
      glassMaterial.reflectionTexture.level = 1;
      (glassMaterial.reflectionTexture as BABYLON.MirrorTexture).adaptiveBlurKernel = 30;

      scene.ambientColor = new BABYLON.Color3(1, 1, 1);
      const gl = new BABYLON.GlowLayer("glow", scene);
      gl.intensity = 0.3;
      BABYLON.SceneLoader.ImportMesh(
        "",
        "3dAssets/",
        "Go1.glb",
        this.scene,
        dogMeshes => {
          this.dogMeshes = dogMeshes;
          const cameraTarget = new BABYLON.Mesh("cameraTarget", this.scene);
          cameraTarget.parent = dogMeshes[0];
          const camera = new BABYLON.FollowCamera(
            "FollowCamera",
            new BABYLON.Vector3(0, 0, 0),
            this.scene,
            cameraTarget
          );
          cameraTarget.rotate(
            new BABYLON.Vector3(0, 1, 0),
            Math.PI / 2,
            BABYLON.Space.LOCAL
          );
          cameraTarget.position = new BABYLON.Vector3(-0.6, 0.8, 0);
          this.camTarget = cameraTarget;

          camera.radius = 1.3;
          camera.fov = 0.95;
          camera.rotationOffset = 0.1;
          camera.heightOffset = 0;
          camera.cameraAcceleration = 0.8;
          setTimeout(() => {
            camera.cameraAcceleration = 0.08;
          }, 2000);
          camera.maxCameraSpeed = 10;
          camera.inputs.clear();
          this.camera = camera;

          if (this.glassMaterial) {
            (this.glassMaterial
              .reflectionTexture as BABYLON.MirrorTexture).renderList = [
              ...dogMeshes,
              ...this.fixedRenderList
            ];
          }
          this.light2.excludedMeshes = dogMeshes;
          for (
            let index = 0;
            index < this.scene.animationGroups.length;
            index++
          ) {
            const group = this.scene.animationGroups[index];
            group.stop();
          }
          this.state = "Walk";
          this.joyStick.lx = 0;
          this.joyStick.ly = 0;
          this.joyStick.ry = 0;
          this.joyStick.rx = 0;
          this.scene.stopAllAnimations();
          this.updateAnimation();
          this.startRendering();
        }
      );
    });
  }
  updateStick(lx: number, ly: number, rx: number, ry: number) {
    this.joyStick.lx = lx;
    this.joyStick.ly = ly;
    this.joyStick.rx = rx;
    this.joyStick.ry = ry;
    this.updateAnimation();
  }
  updateState(state: DogState) {
    this.state = state;
    this.updateAnimation();
  }
  updateAnimation() {
    // this.scene.stopAllAnimations();
    const stateList = ["Static", "Walk", "Run"];
    const stateIndex = stateList.indexOf(this.state);
    for (let index = 0; index < this.scene.animationGroups.length; index++) {
      const group = this.scene.animationGroups[index];
      group.setWeightForAllAnimatables(0);
      if (group.name.indexOf(stateIndex + "") == 0) {
        group.start(true);
      } else {
        group.stop();
      }
    }
    const calculateWeight = (v: number) => ((v + 1) / 2) ** 3;
    this.scene
      .getAnimationGroupByName(stateIndex + "LXP")
      ?.setWeightForAllAnimatables(calculateWeight(this.joyStick.lx));
    this.scene
      .getAnimationGroupByName(stateIndex + "LXN")
      ?.setWeightForAllAnimatables(calculateWeight(-this.joyStick.lx));
    this.scene
      .getAnimationGroupByName(stateIndex + "LYP")
      ?.setWeightForAllAnimatables(calculateWeight(this.joyStick.ly));
    this.scene
      .getAnimationGroupByName(stateIndex + "LYN")
      ?.setWeightForAllAnimatables(calculateWeight(-this.joyStick.ly));
    this.scene
      .getAnimationGroupByName(stateIndex + "RXP")
      ?.setWeightForAllAnimatables(calculateWeight(this.joyStick.rx));
    this.scene
      .getAnimationGroupByName(stateIndex + "RXN")
      ?.setWeightForAllAnimatables(calculateWeight(-this.joyStick.rx));
    this.scene
      .getAnimationGroupByName(stateIndex + "RYP")
      ?.setWeightForAllAnimatables(calculateWeight(this.joyStick.ry));
    this.scene
      .getAnimationGroupByName(stateIndex + "RYN")
      ?.setWeightForAllAnimatables(calculateWeight(-this.joyStick.ry));
  }

  startRendering() {
    this.engine.runRenderLoop(() => {
      this.scene.render();
    });
    this.scene.registerBeforeRender(() => {
      const delta = this.engine.getDeltaTime();
      if (this.state === "Walk") {
        this.dogMeshes[0].movePOV(
          -this.joyStick.ly * delta * 0.0015,
          0,
          this.joyStick.lx * delta * 0.001
        );
        this.dogMeshes[0].rotate(
          new BABYLON.Vector3(0, 1, 0),
          this.joyStick.rx * delta * 0.0005,
          BABYLON.Space.LOCAL
        );
        this.camTarget?.rotate(
          new BABYLON.Vector3(0, 1, 0),
          this.joyStick.rx * delta * 0.0005,
          BABYLON.Space.LOCAL
        );
      }
    });
  }
}
