
import {
  defineComponent,
  ref,
  computed,
  onMounted,
  onBeforeUnmount
} from "vue";
import I18nPlugin from "@/plugins/i18nPlugin";

export default defineComponent({
  name: "KeyboardJoystick",
  props: {
    keyboardJoystickOn: Boolean
  },
  setup(pros, context) {
    const i18n = I18nPlugin.use();
    const usbJoystickOn = ref(false);
    const usbStickAlertOn = ref(false);
    const lx = ref(0);
    const ly = ref(0);
    const rx = ref(0);
    const ry = ref(0);

    const extendButtonOn = ref(false);
    const keys = {
      wPressed: 0,
      aPressed: 0,
      sPressed: 0,
      dPressed: 0,
      upPressed: 0,
      leftPressed: 0,
      downPressed: 0,
      rightPressed: 0,
      spacePressed: 0
    };
    const leftRodStyle = computed(() => {
      return {
        left: "calc(" + ((1 + lx.value) * 35 + 15) + "% - 15px)",
        top: "calc(" + ((1 - ly.value) * 35 + 15) + "% - 15px)"
      };
    });
    const rightRodStyle = computed(() => {
      return {
        left: "calc(" + ((1 + rx.value) * 35 + 15) + "% - 15px)",
        top: "calc(" + ((1 - ry.value) * 35 + 15) + "% - 15px)"
      };
    });
    const handleKey = (key: string) => {
      context.emit("key-update", key);
    };

    const keyListener = (evt: KeyboardEvent) => {
      const updateValue = evt.type === "keydown" ? 1 : 0;
      switch (evt.key) {
        case " ":
          keys.spacePressed = updateValue;
          break;
        case "w":
          keys.wPressed = updateValue;
          break;
        case "a":
          keys.aPressed = updateValue;
          break;
        case "s":
          keys.sPressed = updateValue;
          break;
        case "d":
          keys.dPressed = updateValue;
          break;
        case "ArrowUp":
          keys.upPressed = updateValue;
          break;
        case "ArrowLeft":
          keys.leftPressed = updateValue;
          break;
        case "ArrowDown":
          keys.downPressed = updateValue;
          break;
        case "ArrowRight":
          keys.rightPressed = updateValue;
          break;
        default:
          break;
      }
    };
    const gamepadOnListener = () => {
      usbJoystickOn.value = true;
      usbStickAlertOn.value = true;
      setTimeout(() => {
        usbStickAlertOn.value = false;
      }, 3000);
    };
    const gamepadOffListener = () => {
      usbJoystickOn.value = false;
    };
    let keyUpdateInterval = 0;
    const changeRodValue = (
      prevValue: number,
      posPressed: number,
      negPressed: number
    ) => {
      const updateStep = Math.abs(prevValue) * 0.08 + 0.01;
      const updatedValue =
        prevValue + posPressed * updateStep - negPressed * updateStep;
      const rangedValue =
        updatedValue > 1 ? 1 : updatedValue < -1 ? -1 : updatedValue;
      if (!keys.spacePressed && !posPressed && !negPressed) return 0;
      else return rangedValue;
    };

    onMounted(() => {
      window.addEventListener("keydown", keyListener);
      window.addEventListener("keyup", keyListener);
      window.addEventListener("gamepadconnected", gamepadOnListener);
      window.addEventListener("gamepaddisconnected", gamepadOffListener);
      keyUpdateInterval = window.setInterval(() => {
        if (usbJoystickOn.value) {
          const gamepad = navigator.getGamepads()[0];
          if (gamepad && gamepad.axes.length === 4) {
            lx.value = gamepad.axes[0];
            ly.value = -gamepad.axes[1];
            rx.value = gamepad.axes[2];
            ry.value = -gamepad.axes[3];
            context.emit("stick-update", {
              lx: lx.value,
              ly: ly.value,
              rx: rx.value,
              ry: ry.value
            });
          }
        } else if (pros.keyboardJoystickOn) {
          lx.value = changeRodValue(lx.value, keys.dPressed, keys.aPressed);
          ly.value = changeRodValue(ly.value, keys.wPressed, keys.sPressed);
          rx.value = changeRodValue(
            rx.value,
            keys.rightPressed,
            keys.leftPressed
          );
          ry.value = changeRodValue(ry.value, keys.upPressed, keys.downPressed);
          context.emit("stick-update", {
            lx: lx.value,
            ly: ly.value,
            rx: rx.value,
            ry: ry.value
          });
        }
      }, 30);
    });
    onBeforeUnmount(() => {
      window.removeEventListener("keydown", keyListener);
      window.removeEventListener("keyup", keyListener);
      window.removeEventListener("gamepadconnected", gamepadOnListener);
      window.removeEventListener("gamepaddisconnected", gamepadOffListener);
      window.clearInterval(keyUpdateInterval);
    });
    return {
      lx,
      ly,
      rx,
      ry,
      rightRodStyle,
      leftRodStyle,
      extendButtonOn,
      handleKey,
      i18n,
      usbJoystickOn,
      usbStickAlertOn
    };
  }
});
